.schnabulatorDiv{
  animation: Schnabulator 15s linear infinite ;
  animation-delay: 3s;
  position: fixed;
  right: -20%;
  user-select: none;
  width: 150px; 
  display: "none";
  z-index: 0;
  opacity: 0%;
}

@keyframes Schnabulator {
  0% {
    bottom: 5%;
    right: -20%;
    opacity: 0%;
  }
  1% {
    bottom: 5%;
    right: -20%;
    opacity: 100%;
  }
  25% {
    bottom: 10%;
    opacity: 100%;
    right: 7%;
  }
  35% {
    bottom: 10%;
    opacity: 100%;
    right: 7%;
    transform:  rotateY(0deg) ;
  -webkit-transform: rotateY(0deg) ;
  }
  36% {
    bottom: 10%;
    opacity: 100%;
    right: 7%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
  }
  50% {
    bottom: 30%;
    right: -20%;
    opacity: 100%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
  }
  51% {
    bottom: 30%;
    right: -20%;
    opacity: 0%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
  }

  100%{
    opacity: 0%;
    bottom: 30%;
    right: -20%;
  }
}

.lillyDiv{
  animation: Lilly 35s linear infinite ;
  animation-delay: 3s;
  position: fixed;
  left: -20%;
  user-select: none;
  width: 150px; 
  display: "none";
  z-index: 0;
}

@keyframes Lilly {
  0% {
    bottom: 25%;
    left: -20%;
    opacity: 0%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;

  }
  1% {
    bottom: 25%;
    left: -20%;
    opacity: 100%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
    
  }
  25% {
    bottom: 20%;
    left: 33%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
  }
  35% {
    bottom: 30%;
    left: 120%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;

  }
  36% {
    bottom: 30%;
    left: 120%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;

  }
  50% {
    bottom: 20%;
    left: 120%;
    opacity: 100%;
    transform:  rotateY(0deg) ;
  -webkit-transform: rotateY(0deg) ;

  }
  75% {
    bottom: 30%;
    left: -20%;
    opacity: 100%;
    transform:  rotateY(0deg) ;
  -webkit-transform: rotateY(0deg) ;
  }
  76%{
    opacity: 0%;
    bottom: 30%;
    left: -20%;
    transform:  rotateY(0deg) ;
    -webkit-transform: rotateY(0deg) ;
  }

  100%{
    opacity: 0%;
    bottom: 30%;
    left: -20%;
    transform:  rotateY(0deg) ;
    -webkit-transform: rotateY(0deg) ;
  }
}


