.schnabulatorDiv{
  -webkit-animation: Schnabulator 15s linear infinite ;
          animation: Schnabulator 15s linear infinite ;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
  position: fixed;
  right: -20%;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 150px; 
  display: "none";
  z-index: 0;
  opacity: 0%;
}

@-webkit-keyframes Schnabulator {
  0% {
    bottom: 5%;
    right: -20%;
    opacity: 0%;
  }
  1% {
    bottom: 5%;
    right: -20%;
    opacity: 100%;
  }
  25% {
    bottom: 10%;
    opacity: 100%;
    right: 7%;
  }
  35% {
    bottom: 10%;
    opacity: 100%;
    right: 7%;
    transform:  rotateY(0deg) ;
  -webkit-transform: rotateY(0deg) ;
  }
  36% {
    bottom: 10%;
    opacity: 100%;
    right: 7%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
  }
  50% {
    bottom: 30%;
    right: -20%;
    opacity: 100%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
  }
  51% {
    bottom: 30%;
    right: -20%;
    opacity: 0%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
  }

  100%{
    opacity: 0%;
    bottom: 30%;
    right: -20%;
  }
}

@keyframes Schnabulator {
  0% {
    bottom: 5%;
    right: -20%;
    opacity: 0%;
  }
  1% {
    bottom: 5%;
