  }
  51% {
    bottom: 30%;
    right: -20%;
    opacity: 0%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
  }

  100%{
    opacity: 0%;
    bottom: 30%;
    right: -20%;
  }
}

